.newNote {
  background-color: white;
  width: 50%;
  border-radius: 20px;
  border: 2px solid rgb(175, 175, 175);
  margin: 10px auto;
  min-height: 300px;
}

.title {
  background-color: transparent;
  margin-top: 8px;
  min-width: 90%;
  font-size: 18px;
  padding: 5px;
  border-style: none none solid none;
  outline: none;
}

.text {
  font-size: 14px;
  float: right;
  background-color: transparent;
  border: none;
  text-align: start;
  min-width: 95%;
  min-height: 200px;
  padding: 5px;
  resize: none;
  outline: none;
  scrollbar-width: thin;
  scrollbar-color: rgb(100, 100, 100) transparent;
}

.colorBtn {
  float: left;
  border: none;
  width: 40px;
  height: 40px;
  padding: 10px;
  border-radius: 50%;
  margin: 5px 2px 2px 2px;
  font-weight: bold;
  font-size: 16px;
  color: rgba(0, 0, 0, 0.6);
}

.colorBtn:hover {
  opacity: 0.8;
}

.colorBtn:nth-child(3) {
  margin-left: 20px;
}

.submitBtn {
  float: right;
  border: 2px solid rgb(0, 175, 0);
  background-color: rgb(68, 199, 103);
  color: white;
  font-weight: bold;
  font-size: 14px;
  padding: 8px 40px;
  margin: 5px 15px 2px 2px;
  border-radius: 20px;
}

.tick {
  font-weight: bolder;
  font-size: 16px;
  height: 20px;
  width: 20px;
  opacity: 0.7;
}

.submitBtn:hover {
  background-color: rgba(68, 199, 103, 0.9);
  border: 2px solid green;
}

@media (max-width: 820px) {
  .newNote {
    width: 60%;
  }
}

@media (max-width: 767px) {
  .newNote {
    width: 70%;
  }

  .colorBtn {
    width: 34px;
    height: 34px;
    padding: 7px;
    margin: 8px 2px 2px 2px;
  }

  .colorBtn:nth-child(3) {
    margin-left: 10px;
  }

  .submitBtn {
    padding: 7px 30px;
    margin: 8px 10px 2px 2px;
  }
}

@media (max-width: 479px) {
  .newNote {
    width: 95%;
  }

  .colorBtn {
    width: 26px;
    height: 26px;
    padding: 3px;
    margin: 12px 2px 2px 2px;
  }

  .colorBtn:nth-child(3) {
    margin-left: 7px;
  }

  .submitBtn {
    padding: 4px 20px;
    margin: 12px 7px 2px 2px;
  }
}
